.loan {
    display: block;
}

.loan > .header {
    width: 100%;
    background-image: url('../images/contact.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    height: 300px;
    position: relative;
    margin: 0;
    display: flex;
    align-items:  center;
    justify-content: center;
}

.loan > .header::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #043eaa67;
    z-index: 0;
}

.loan input,
.loan select {
    border-radius: 10px;
}

.loan .calc {
    display: inline-block;
    margin-left: 4px;
    color: rgba(43, 0, 0, 0.856);
}

#regForm {
    background-color: #ffffff;
    margin: 50px auto;
    padding: 40px;
    width: 60%;
    box-shadow: 0 0 9px 5px rgba(41, 86, 222, 0.21);
    min-width: 300px;
}
  
.loan h1 {
    text-align: center;
}
  
.champ input, .champ select{
    padding: 10px;
    width: 100%;
    font-size: 17px;
    font-family: Raleway;
    border: 1px solid #aaaaaa;
    margin:10px 0;
}
  
  /* Mark input boxes that gets an error on validation: */
.champ input.invalid, .champ select.invalid {
    background-color: #ffdddd;
}
  
.champ input[type=radio]{
    width: 35px;
    display: inline-block;
    text-align: left;
}
.loan h5{
    font-size: 14px;
    font-family: Raleway;
}
  /* Hide all steps by default: */
.champ {
    display: none;
}
  
.loan button {
    background-color: #04AA6D;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    font-size: 17px;
    font-family: Raleway;
   cursor: pointer;
}
  
button:hover {
    opacity: 0.8;
}
  
#prevBtn {
    background-color: #bbbbbb;
}
  
  /* Make circles that indicate the steps of the form: */
.loan .step {
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbbbbb;
    border: none;
    border-radius: 50%;
    display: inline-block;
    opacity: 0.5;
}
  
.loan .step.active {
    opacity: 1;
}
  
  /* Mark the steps that are finished and valid: */
.loan .step.finish {
    background-color: #04AA6D;
}
  
  /*autocomplete country regForm*/
  /*the container must be positioned relative:*/
.loan .autocomplete {
    position: relative;
}
  
  
.loan .autocomplete-items {
    position: absolute;
    border: 1px solid #d4d4d4;
    border-bottom: none;
    border-top: none;
    z-index: 99;
    /*position the autocomplete items to be the same width as the container:*/
    top: 100%;
    left: 0;
    right: 0;
}
  
.loan .autocomplete-items div {
    padding: 10px;
    cursor: pointer;
    background-color: #fff;
    border-bottom: 1px solid #d4d4d4;
}
  
  /*when hovering an item:*/
.loan .autocomplete-items div:hover {
    background-color: #e9e9e9;
}
  
  /*when navigating through the items using the arrow keys:*/
.autocomplete-active {
    background-color: DodgerBlue !important;
    color: #ffffff;
}
  
.loan .alert{
    width: 100%;
    height: 40px;
    text-align: center;
    font-size: 16px;
}

.loan > .agreement {
    background-color: #0439aa54;
    padding: 20px;
    margin-bottom: -10px;
    color: rgba(255, 255, 255, 0.74);
}
  