.footer {
    padding-top: 100px;
    background-image: url('../images/footerbg.png');
    background-size: cover;
    background-repeat: no-repeat;
    color: white;
    opacity: 0.8;
}

.footer ul > li {
    list-style: none;
    margin: 8px 0;
}

.footer ul > li:hover a {
    text-decoration: underline;
    font-weight: 600;
}

.footer ul > li > a {
    text-decoration: none;
}