.header {
    background-image: url('../images/services.png');
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    width: 100%;
    min-height: 300px;
    padding-left: 15%;
    height: 50vh;
}

.header > h1 {
    font-size: 60px;
    font-weight: bold;
    letter-spacing: 1px;
    display: block;
}

.header > p {
    text-transform: uppercase;
    letter-spacing: 10px;
    color: rgba(6, 199, 38, 0.637);
}

.steps > div svg {
    font-size: 45px;
}

.steps > div img {
    height: 50px;
    width: 60px
}

.steps > div {
    background-color: white;
    padding: 20px;
    margin: 10px;
    width: 400px;
    border-radius: 20px;
    box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.438);
}

.joy > img{
    width: 100%;
    height: 600px;
}

.j_text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.j_text > p {
    font-weight: 400;
    font-size: 20px;
}

.j_text > h2 {
    font-weight: 600;
}

@media screen and (max-width: 900px) {
    .header {
        height: 200px;
        min-height: 200px;
    }
    .header > h1 {
        font-size: 35px;
    }

    .header > p {
        letter-spacing: 5px;
        font-size: 8px;
        margin-top: -10px;
    }
}