*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.offer {
    display: flex;
    justify-content: space-between;
    width: 60%;
    margin: 0 auto;
    transform: translateY(20px);
    transition: 1s all ease-in;
}

.offer > div svg {
    font-size: 50px;
}

.offer > div h3 {
    font-size: 50px;
    font-size: 23px;
}

.offer > div {
    width: 35%;
    display: inline-block;
    background-color: white;
    background: radial-gradient(at 0% 100%, white 60%, rgb(15, 46, 186) 266%);
    box-shadow: 0 0 9px 4px rgba(41, 86, 222, 0.21);
    padding: 10px;
    text-align: center;
    border-radius: 4px;
}

.offer_title {
    margin: 30px 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 100px;
}

.offer_title > h3 {
    font-size: 40px;
    letter-spacing: 1px;
    font-weight: 400;
}

.serv_box {
    justify-content: center;
}

.offer_title > p {
    color: #7A838B;
    font-weight: 400;
}

.cards svg {
    font-size: 100px;
    color: rgba(1, 66, 1, 0.774);
    z-index: 10;
}

.service {
    margin: 0 10px;
    width: 350px;
    gap: 4px;
    padding: 20px;
    border: none !important;
}

.service_header {
    background-image: radial-gradient(ellipse at top, rgba(255, 255, 255, 0.801), rgba(77, 77, 243, 0.664), transparent),
    radial-gradient(ellipse at bottom, rgba(0, 0, 255, 0.726), rgba(77, 77, 243, 0.664), transparent);
    height: 100px;
    border-radius: 10px;
    animation: flash 25s ease infinite alternate;
    color: white;
}

.service_icon {
    margin: 0 auto;
    text-align: center;
}

.service_header > div {
    transform: translateY(-80px);
}

.service_content > ul li,
.service_content > ol li {
    list-style: none;
    margin: 10px 0;
}

.service_content {
    width: 100%;
    box-shadow: 0 10px 9px 4px rgba(41, 86, 222, 0.21);
    padding: 10px;
}

.service_content > ul li::marker {
    content: "â–º ";
    color: blue;
    font-weight: bold;
}

.service_content > ul li:hover::marker {
    content: 'ðŸ§”ðŸ¾';
}

ol {
    counter-reset: my-awesome-counter;
  }
  
  ol li {
    counter-increment: my-awesome-counter;
  }
  
  ol li::before {
    content: counter(my-awesome-counter) ". ";
    color: blue;
  }

  .cards div > button:hover {
    border: 1px solid blue !important;
    color: blue !important;
    background-color: white !important;
    transform: translate(2px,3px);
  }

  @keyframes flash {
    0% {background-position: 0% 100%}
    25% {background-position:25% 100%}
    51% {background-position: 50% 100%}
    100% {background-position:100% 100%}
  }

  @media screen and (max-width: 900px) {
    .offer {
        width: 100%;
        justify-content: center;
        gap: 30px;
    }

    .offer > div {
        width: 80%;
    }

    .service {
        margin: 30px 0;
    }
  }