.header {
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    width: 100%;
    min-height: 500px;
    height: 70vh;
    z-index: 0;
}

.header > img {
    width: 100%;
    height: 100%;
}

.h_title {
    position: absolute;
    bottom: 0%;
    width: 800px;
    left: 20%;
    background-color: blue;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 800px;
    border-radius: 50%;
    transform: translate(100%, 100%);
    transition: 2s transform;
    padding-top: 50px;
    z-index: 10;
    opacity: 0;
}

.h_title > div {
    text-align: center;
    width: 70%;
    color: white;
}

.h_title > div > p {
    font-size: 25px;
    font-weight: 2px;
}

.mask {
    background-color: white;
    z-index: 0;
    position: relative;
}

.testimonials {
    background-color: rgb(8, 39, 85);
    display: block;
    width: 100%;
    height: auto;
}

@media screen and (max-width: 900px) {
    .header {
        height: 200px;
        min-height: 200px;
    }

    .h_title {
        width: 80%;
        margin: 0 auto;
        left: 10%;
        height: 400px;
        padding-top: 50px;
    }

    .h_title > div {
        width: 80%;
        margin-top: -20px;
    }
    
    .h_title > div > p {
        font-size: 14px;
        font-weight: 1px;
    }
}