.carousel {
    background: none;
    width: 70%;
    height: 350px;
    margin: 0 auto;
    transition: 1s all;
}

.carousel .active {
    border: none;
}

.carousel h2 {
    color: white;
    display: block;
    margin: 10px auto;
    width: 100px;
}

.carousel-item {
    /* display: grid; */
    background:none;
    margin-top: 10px;
    justify-content: space-between;
}

.block {
    width: 28%;
    z-index: 0;
    position: relative;
    left: 15%;
}

.block > img {
    width: 100%;
}

.carousel-caption {
    top: 20%;
    width: 40%;
    left: 50%;
}

@media screen and (max-width: 900px) {
    .carousel {
        height: 200px;
        width: 100%;
    }

    .carousel-control-prev {
        left: 0;
    }

    .carousel-control-next {
        right: 0;
    }

    .carousel-caption {
        width: 60%;
        left: 35%;
        top: -20px;
        overflow-y: scroll;
    }

    .carousel-caption > p {
        font-size: 14px;
        font-size: 300;
    }

    .carousel-caption > h5 {
        font-size: 14px;
        font-weight: bold;
    }

    .block {
        left: 10%;
    }
}