.header {
	display: block;
	width: 100%;
	min-height: 640px;
	height: calc(100vh - 70px);
	background-image: url("../images/bkra-header.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	z-index: 0;
}

.woman {
	position: absolute;
	top: 0;
	right: 20px;
	float: right;
	height: 100%;
	z-index: -100;
}

.header h1 {
	font-weight: 600;
	font-size: 60px;
	letter-spacing: 2px;
	line-height: 1;
	color: white;
	text-transform: uppercase;
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.title>h2 {
	color: white;
	font-family: 'Courier New', Courier, monospace;
	font-size: 30px;
}

.title>img {
	width: 200px;
	display: block;
	margin: 0 auto;
}

.fbox {
	position: relative;
	width: 400px;
	margin: 50px auto 0 auto;
}

.fbox::before {
	content: '';
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 20px;
	left: 20px;
	background-color: rgb(0, 128, 55);
	border-radius: 5px;
	z-index: -9;
}

.circle {
	height: 46px;
	width: 46px;
	line-height: 60px;
	border-radius: 50%;
	background-color: #fff;
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	bottom: 10%;
	left: 45%;
}

.circle svg {
	color: #4badf7;
	font-size: 40px;
}

.circle {
	-webkit-animation: ripple 0.5s ease infinite alternate-reverse;
	animation: ripple 0.5s ease infinite alternate-reverse;
}

.circle {
	-webkit-animation: ripple 0.5s ease 1s infinite alternate-reverse;
	animation: ripple 0.5s ease 1s infinite alternate-reverse;
}

@-webkit-keyframes ripple {
	0% {
		-webkit-transform: translateY(-4px);
	}

	75% {
		-webkit-transform: translateY(-10);
	}

	100% {
		-webkit-transform: translateY(0);
	}
}

@keyframes ripple {
	0% {
		transform: translateY(-4px);
	}

	75% {
		transform: translateY(-10);
	}

	100% {
		transform: translateY(0);
	}
}

@media screen and (max-width: 900px) {
	.woman {
		display: none;
	}

	.header {
		height: auto;
		display: grid;
		justify-content: center;
		align-items: center;
		padding-bottom: 50px;
	}

	.header h1 {
		font-size: 30px;
	}

	.title {
		margin: 15% 0 10% 0;
	}

	.title > h2 {
		font-size: 18px;
	}

	.title > img {
		display: none;
	}

	.fbox {
		position: relative;
		margin: 0;
		width: 100%;
	}

	.circle {
		display: none;
	}
}