.image {
    height: 100%;
    width: 100%;
    transition: 2s transform;
    transform: scale(1.5, 1.5);
    opacity: 0;
}

.about_info {
    transform: translateY(-100px);
    opacity: 0;
    transition: 2s transform;
}

.about_info > h3 {
    font-size: 50px;
    font-weight: 400;
    letter-spacing: 2px;
    transform: s;
}

.about_info > ul li {
    margin: 10px 0;
    font-size: 20px;
}

.about_info > ul li::marker {
    color: blue;
}